          <div class="tag-cloud">
            <el-tag 
              v-for="tag in tags" 
              :key="tag.name"
              :type="activeTag === tag.name ? 'primary' : 'info'"
              effect="plain"
              class="tag-item"
              @click="setTag(tag.name)"
            >
              {{ tag.name }}
            </el-tag>
          </div>
        </div>
      </div>
      
      <div class="blog-main">
        <div class="blog-filters animate__animated animate__fadeIn">
          <div class="filter-info">
            <template v-if="activeCategory || activeTag || searchQuery">
              <span>Á≠õÈÄâÊù°‰ª∂: </span>
              <el-tag v-if="activeCategory" closable @close="clearCategory">{{ getCategoryName(activeCategory) }}</el-tag>
              <el-tag v-if="activeTag" closable @close="clearTag">{{ activeTag }}</el-tag>
              <el-tag v-if="searchQuery" closable @close="clearSearch">ÊêúÁ¥¢: {{ searchQuery }}</el-tag>
            </template>
            <span v-else>ÊòæÁ§∫ÂÖ®ÈÉ®ÊñáÁ´†</span>
          </div>
          <div class="sort-control">
            <span>ÊéíÂ∫è: </span>
            <el-select v-model="sortBy" size="small">
              <el-option label="ÊúÄÊñ∞ÂèëÂ∏É" value="date-desc" />
              <el-option label="ÊúÄÊó©ÂèëÂ∏É" value="date-asc" />
              <el-option label="ÈòÖËØªÊúÄÂ§ö" value="views-desc" />
            </el-select>
          </div>
        </div>
        
        <div class="blog-list">
          <div 
            v-for="(article, index) in filteredArticles" 
            :key="article.id"
            class="blog-card animate__animated animate__fadeInUp"
            :style="{'animation-delay': `${index * 0.1}s`}"
          >
            <div class="blog-card-content">
              <div class="blog-meta">
                <span class="blog-date">{{ formatDate(article.date) }}</span>
                <span class="blog-category">{{ getCategoryName(article.category) }}</span>
              </div>
              <h2 class="blog-title">{{ article.title }}</h2>
              <p class="blog-excerpt">{{ article.excerpt }}</p>
              <div class="blog-footer">
                <div class="blog-tags">
