                    v-for="tag in article.tags" 
                    :key="tag" 
                    size="small"
                    effect="plain"
                  >
                    {{ tag }}
                  </el-tag>
                </div>
                <div class="blog-stats">
                  <span class="blog-views">{{ article.views }} ÈòÖËØª</span>
                  <span class="blog-comments">{{ article.comments }} ËØÑËÆ∫</span>
                </div>
              </div>
              <el-button type="primary" plain class="read-more-btn" @click="$router.push(`/blog/${article.id}`)">ÈòÖËØªÂÖ®Êñá</el-button>
            </div>
          </div>
        </div>
        
        <div class="pagination-container" v-if="filteredArticles.length > 0">
          <el-pagination
            background
            layout="prev, pager, next"
            :total="filteredArticles.length"
            :page-size="6"
            @current-change="handlePageChange"
          />
        </div>
        
        <div class="no-results" v-if="filteredArticles.length === 0">
          <div class="no-results-icon">üìù</div>
          <p>Ê≤°ÊúâÊâæÂà∞Á¨¶ÂêàÊù°‰ª∂ÁöÑÊñáÁ´†</p>
          <el-button type="primary" @click="resetFilters">Ê∏ÖÈô§Á≠õÈÄâÊù°‰ª∂</el-button>
        </div>
      </div>
    </div>
  </div>
