<script setup name="BlogPage">
import { ref, computed } from 'vue';

// ÊêúÁ¥¢Êü•ËØ¢
const searchQuery = ref('');

// ÂàÜÁ±ªÊï∞ÊçÆ
const categories = ref([
  { id: 1, name: 'JavaÂºÄÂèë', count: 15 },
  { id: 2, name: 'Á≥ªÁªüÊû∂ÊûÑ', count: 8 },
  { id: 3, name: 'ÊÄßËÉΩ‰ºòÂåñ', count: 12 },
  { id: 4, name: 'ÂæÆÊúçÂä°', count: 10 },
]);

// Ê†áÁ≠æÊï∞ÊçÆ
const tags = ref([
  { name: 'Spring Boot' },
  { name: 'Spring Cloud' },
  { name: 'Docker' },
  { name: 'Kubernetes' },
  { name: 'MySQL' },
  { name: 'Redis' },
]);

// ÊñáÁ´†Êï∞ÊçÆ
const articles = ref([
  {
    id: 1,
    title: 'Spring Boot ÊúÄ‰Ω≥ÂÆûË∑µÊåáÂçó',
    excerpt: 'Êú¨Êñá‰ªãÁªç‰∫ÜSpring BootÈ°πÁõÆ‰∏≠ÁöÑ‰∏Ä‰∫õÊúÄ‰Ω≥ÂÆûË∑µÔºåÂåÖÊã¨È°πÁõÆÁªìÊûÑ„ÄÅÈÖçÁΩÆÁÆ°ÁêÜ„ÄÅ‰æùËµñÊ≥®ÂÖ•ÊñπÈù¢ÁöÑÂª∫ËÆÆ„ÄÇ',
    date: '2023-05-15',
    category: 1,
    tags: ['Spring Boot', 'Java'],
    views: 1250,
    comments: 18
  },
  {
    id: 2,
    title: 'ÂæÆÊúçÂä°Êû∂ÊûÑËÆæËÆ°‰∏éÂÆûË∑µ',
    excerpt: 'Êé¢ËÆ®ÂæÆÊúçÂä°Êû∂ÊûÑÁöÑÊ†∏ÂøÉÊ¶ÇÂøµ„ÄÅËÆæËÆ°ÂéüÂàôÂíåÊúÄ‰Ω≥ÂÆûË∑µÔºåÂåÖÊã¨ÊúçÂä°ÊãÜÂàÜ„ÄÅÈÄö‰ø°Êú∫Âà∂„ÄÅÊúçÂä°Ê≤ªÁêÜÁ≠âÂÖ≥ÈîÆËØùÈ¢ò„ÄÇ',
    date: '2023-06-01',
    category: 2,
    tags: ['ÂæÆÊúçÂä°', 'Spring Cloud', 'Docker'],
    views: 980,
    comments: 15
  },
  {
    id: 3,
    title: 'MySQLÊÄßËÉΩË∞É‰ºòÂÆûÊàòÊåáÂçó',
    excerpt: 'Ê∑±ÂÖ•ÂàÜÊûêMySQLÊÄßËÉΩ‰ºòÂåñÊäÄÂ∑ßÔºåÂåÖÊã¨Á¥¢Âºï‰ºòÂåñ„ÄÅÊü•ËØ¢‰ºòÂåñ„ÄÅÈÖçÁΩÆË∞É‰ºòÁ≠âÂÆûÁî®ÊäÄÂ∑ßÔºåÂ∏ÆÂä©ÊèêÂçáÊï∞ÊçÆÂ∫ìÊÄßËÉΩ„ÄÇ',
    date: '2023-06-15',
    category: 3,
    tags: ['MySQL', 'ÊÄßËÉΩ‰ºòÂåñ', 'Êï∞ÊçÆÂ∫ì'],
    views: 850,
    comments: 12
  },
  {
    id: 4,
    title: 'KubernetesÂÆπÂô®ÁºñÊéíÂÆûË∑µ',
    excerpt: 'ËØ¶ÁªÜ‰ªãÁªçKubernetesÁöÑÊ†∏ÂøÉÊ¶ÇÂøµ„ÄÅÊû∂ÊûÑËÆæËÆ°ÂíåÂÆûË∑µÁªèÈ™åÔºåÂåÖÊã¨ÂÆπÂô®ÁºñÊéí„ÄÅÊúçÂä°ÂèëÁé∞„ÄÅË¥üËΩΩÂùáË°°Á≠âÂÜÖÂÆπ„ÄÇ',
    date: '2023-07-01',
    category: 4,
    tags: ['Kubernetes', 'Docker', 'ÂæÆÊúçÂä°'],
    views: 760,
    comments: 10
  },
  {
    id: 5,
    title: 'JavaÂπ∂ÂèëÁºñÁ®ãÊ∑±Â∫¶Ëß£Êûê',
    excerpt: 'Ê∑±ÂÖ•Êé¢ËÆ®JavaÂπ∂ÂèëÁºñÁ®ãÁöÑÊ†∏ÂøÉÊ¶ÇÂøµ„ÄÅÁ∫øÁ®ãÂÆâÂÖ®„ÄÅÈîÅÊú∫Âà∂„ÄÅÁ∫øÁ®ãÊ±†Á≠âÈáçË¶Å‰∏ªÈ¢òÔºåÊèê‰æõÂÆûÊàòÁªèÈ™åÂàÜ‰∫´„ÄÇ',
    date: '2023-07-15',
    category: 1,
    tags: ['Java', 'Âπ∂ÂèëÁºñÁ®ã', 'ÊÄßËÉΩ‰ºòÂåñ'],
    views: 920,
    comments: 14
  }
]);

// Á≠õÈÄâÂíåÊéíÂ∫èÁä∂ÊÄÅ
const activeCategory = ref(null);
const activeTag = ref(null);
const sortBy = ref('date-desc');

// ËÆ°ÁÆóÂ±ûÊÄßÔºöËøáÊª§ÂêéÁöÑÊñáÁ´†ÂàóË°®
const filteredArticles = computed(() => {
  let result = articles.value;

  // Â∫îÁî®ÂàÜÁ±ªÁ≠õÈÄâ
  if (activeCategory.value) {
    result = result.filter(article => article.category === activeCategory.value);
  }

  // Â∫îÁî®Ê†áÁ≠æÁ≠õÈÄâ
  if (activeTag.value) {
    result = result.filter(article => article.tags.includes(activeTag.value));
  }

  // Â∫îÁî®ÊêúÁ¥¢Á≠õÈÄâ
  if (searchQuery.value) {
    const query = searchQuery.value.toLowerCase();
    result = result.filter(article =>
      article.title.toLowerCase().includes(query) ||
      article.excerpt.toLowerCase().includes(query)
    );
  }

  // Â∫îÁî®ÊéíÂ∫è
  result = [...result].sort((a, b) => {
    switch (sortBy.value) {
      case 'date-desc':
        return new Date(b.date) - new Date(a.date);
      case 'date-asc':
        return new Date(a.date) - new Date(b.date);
      case 'views-desc':
        return b.views - a.views;
      default:
        return 0;
    }
  });

  return result;
});

// ÊñπÊ≥ï
const setCategory = (categoryId) => {
  activeCategory.value = categoryId;
};

const setTag = (tagName) => {
  activeTag.value = tagName;
};

const clearCategory = () => {
  activeCategory.value = null;
};

const clearTag = () => {
  activeTag.value = null;
};

const clearSearch = () => {
  searchQuery.value = '';
};

const resetFilters = () => {
  activeCategory.value = null;
  activeTag.value = null;
  searchQuery.value = '';
  sortBy.value = 'date-desc';
};

const getCategoryName = (categoryId) => {
  const category = categories.value.find(c => c.id === categoryId);
  return category ? category.name : '';
};

const formatDate = (dateString) => {
  return new Date(dateString).toLocaleDateString('zh-CN');
};

const handlePageChange = (page) => {
  // Â§ÑÁêÜÂàÜÈ°µÈÄªËæë
  console.log('ÂΩìÂâçÈ°µÁ†ÅÔºö', page);
};
</script>

